import * as React from 'react';
import { useState, useEffect } from 'react';
import styled from 'styled-components';
import * as constants from '../../contsants';

export const Bio: React.FunctionComponent<{}> = () => {
  return (
    <>
    <PerspectiveContainer id='perspectiveWrapper'>
      <BioWrapper id='perspectiveRecipient'>
        <Title>{constants.bio.title}</Title>
        <Body>
          {constants.bio.body}&nbsp;For now, feel free to view the source code
          for the page in it's current state&nbsp;
          <A href="https://github.com/SmithKy3/Kyesmith.me-src" target="_blank">
            here.
          </A>
        </Body>
      </BioWrapper>
    </PerspectiveContainer>
    <FeatureWall/>
    </>
  );
};

const PerspectiveContainer = styled.div`
  position: fixed;
  z-index: -1;
  top: 20vh;
  left: 30vw;
  width: 40vw;
  height: 50vh;
  perspective: 50px;

  @media screen and (max-width: 600px) {
    width: 70vw;
    height: 60vh;
    left: 15vw;
  }
`;

const BioWrapper = styled.div`
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  border: 3px solid ${constants.colors.theme.cloud};
  transform: rotateX(0deg) rotateY(0deg);
  transition: transform 0.25s;
`;

const Title = styled.div`
  position: absolute;
  top: 10%;
  left: 0;
  width: 100%;
  height: 10%;

  text-align: center;
  font-family: Calistoga;
  font-size: 2vw;
  color: ${constants.colors.theme.stormBlue};

  @media screen and (max-width: 600px) {
    font-size: 3vw;
  }
`;

const Body = styled.div`
  position: absolute;
  top: 25%;
  left: 20%;
  width: 60%;
  height: content-max;
  border-width: 0 0 0 10px;
  border-color: ${constants.colors.theme.teal};

  text-align: justify;
  font-family: 'Baloo Chettan 2';
  font-weight: 400;
  font-size: 1.25vw;
  color: ${constants.colors.theme.stormBlue};

  @media screen and (max-width: 600px) {
    font-size: 2.5vw;
  }
`;

const A = styled.a`
  color: ${constants.colors.theme.stormBlue};
  text-decoration: underline;

  &:hover {
    cursor: pointer;
    color: ${constants.colors.theme.pinkSand};
  }
`;
